export default function Blocos() {
  //  const styles = useStyles();
  let produtos = useSelector(state => state.products.grouped_products);
  produtos = produtos.filter(produto => {
    return produto.categories[0].id === 17;
  });
  return (
    <div>
      <Trilha pagename="Blocos" />

      <h1 className="titulo-internas">Blocos</h1>

      <div className="entry-content">
        <p>
          Os Blocos do Carnaval são as grandes atrações do Carnaval. Os blocos
          tem seu espaço delimitado por uma grande corda e esquema próprio de
          segurança. Local onde os grandes astros da música brasileira se
          apresentam em cima do Trio Elétrico, fazendo a alegria de todos no
          Carnaval de Salvador.
        </p>
        <p>
          <b>
            Confira abaixo os principais Blocos e clique para informações dos
            Blocos do Carnaval de Salvador 2020.
          </b>
        </p>
        <Grid container>
          {produtos.map(item => (
            <Grid item xs={12} key={item.id}>
              <Link key={item.id} to="/compre-aqui" component={RouterLink}>
                <Produto dados={item} tamanho={"100%"} link={"compre-aqui"} />
              </Link>
            </Grid>
          ))}
        </Grid>
      </div>
    </div>
  );
}
